
.custom-loader {
  -webkit-animation: loader 1s infinite;
          animation: loader 1s infinite;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
@-webkit-keyframes loader {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes loader {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
